import * as React from 'react';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Typography from '@mui/material/Typography';
import { Box } from '@mui/material';
import './Modales.css'



const BootstrapDialog = styled(Dialog)(({ theme }) => ({
    '& .MuiDialogContent-root': {
        padding: theme.spacing(2),
    },
    '& .MuiDialogActions-root': {
        padding: theme.spacing(1),
    },
}));

export default function CustomizedDialogs(props) {



    const handleClickOpen = () => {
        props.setOpen(true);
    };
    const handleClose = () => {
        props.setOpen(false);
    };

    const handleBotonClick = (url) => {
        // Cambia la ubicación de la página al hacer clic en el botón
        window.location.href = url;
      };

    return (
        <div>

            <BootstrapDialog
                onClose={handleClose}
                aria-labelledby="customized-dialog-title"
                open={props.open}
                fullWidth
            >
                <DialogTitle sx={{ m: 0, p: 2 }} id="customized-dialog-title">

                </DialogTitle>
                <IconButton
                    aria-label="close"
                    onClick={handleClose}
                    sx={{
                        position: 'absolute',
                        right: 8,
                        top: 8,
                        color: (theme) => theme.palette.grey[500],
                    }}
                >
                    <CloseIcon />
                </IconButton>
                <DialogContent>
                    <h3 className="title-modal">Selecciona tu rol</h3>
                    <div className="o-botones">
                        <button onClick={() => handleBotonClick('https://scenariojobs.scenariovr.co/')} 
                            className="btn-modal">Candidato <br/>Aplicar Assessment</button>
                        <button onClick={() => handleBotonClick('https://admin.scenariojobs.scenariovr.co/')} 
                            className="btn-modal">Profesional de talento humano <br/> Headhunter</button>
                    </div>
                    <div className="modal-shap"></div>

                </DialogContent>

            </BootstrapDialog>
        </div>
    );
}