import React from 'react';
import { Card, CardHeader, CardContent, Typography, Button, List, ListItem, ListItemText, Divider } from '@mui/material';

function PlanCard({ title, price, description, features }) {
  return (
    <Card variant="outlined" sx={{ backgroundColor: 'white', borderRadius: 2, border:"none" }}>
      <CardHeader
        sx={{ backgroundColor: '#fe6600', color: 'white', textAlign:"center" }}
        title={title}
      />
      <CardContent sx={{ textAlign:"center" }}>
        <Typography variant="h6" color="textPrimary" gutterBottom>
          {price}
        </Typography>
        <Typography variant="body2" color="textSecondary" paragraph>
          {description}
        </Typography>
        <Button variant="contained" color="primary" fullWidth>
          Inscribirse
        </Button>
        <Divider variant="middle" sx={{m:"1rem 0rem"}}/>
        <List>
          {features.map((feature, index) => (
            <ListItem key={index}>
              <ListItemText primary={feature} />
            </ListItem>
          ))}
        </List>
      </CardContent>
    </Card>
  );
}

export default PlanCard;