import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import Divider from '@mui/material/Divider';
import AdbIcon from '@mui/icons-material/Adb';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';

import CustomizedDialogs from '../Modales/CustomDialog';

const pages = ['Inicio', 'Nosotros','Precios' ,'Servicios', 'Contacto'];
const settings = ['Profile', 'Account', 'Dashboard', 'Logout'];

function ResponsiveAppBar() {
    const [openDialog, setOpenDialog] = React.useState(false);
    const [anchorElNav, setAnchorElNav] = React.useState(null);
    const [anchorElUser, setAnchorElUser] = React.useState(null);

    const handleOpenNavMenu = (event) => {
        setAnchorElNav(event.currentTarget);
    };

    const handleCloseNavMenu = () => {
        setAnchorElNav(null);
    };

    function scrollToSection(id) {

            handleCloseNavMenu()
            const mysection = document.getElementById("section-"+id);
            mysection?.scrollIntoView({ behavior: 'smooth' });

    }
    
      const abrirWhatsApp = () => {
        const telefono = '+573154162122'; // Número de teléfono (debes incluir el código de país)
        const mensaje = '¡Hola! Estoy interesado en los servicios de Scenario'; // Mensaje predefinido
        const url = `https://wa.me/${telefono}?text=${encodeURIComponent(mensaje)}`;
    
        // Abre la URL de WhatsApp en una nueva ventana o pestaña
        window.open(url, '_blank');
      };



    return (
        <AppBar position="fixed" sx={{ backgroundColor: "white", color: "dimgray" }}>
            <Container maxWidth="xl">
                <Toolbar disableGutters>

                    <IconButton onClick={()=>scrollToSection("Inicio")} aria-label="Ver imagen" sx={{ display: { xs: 'none', md: 'flex' } }}>
                        <img src={process.env.PUBLIC_URL + "/Assets/logos/scenario-logo-color.png"} alt="logo scenario" style={{ height: '50px', width: 'auto',marginRight:"3rem" }} />
                    </IconButton>


                    {/* Menú ICON */}
                    <Box sx={{ flexGrow: 0, display: { xs: 'flex', md: 'none' } }}>
                        <IconButton
                            size="large"
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            aria-haspopup="true"
                            onClick={handleOpenNavMenu}
                            color="inherit"
                        >
                            <MenuIcon />
                        </IconButton>
                        <Menu
                            id="menu-appbar"
                            anchorEl={anchorElNav}
                            anchorOrigin={{
                                vertical: 'bottom',
                                horizontal: 'left',
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'left',
                            }}
                            open={Boolean(anchorElNav)}
                            onClose={handleCloseNavMenu}
                            sx={{
                                display: { xs: 'block', md: 'none' },
                            }}
                        >
                            {pages.map((page) => (
                                <MenuItem key={page} onClick={()=>scrollToSection(page)}>
                                    <Typography textAlign="center">{page}</Typography>
                                </MenuItem>
                            ))}
                            <Divider variant="middle" />
                            <MenuItem key={"Tienda"} onClick={()=>scrollToSection("Tienda")}>
                                    <Typography textAlign="center">Tienda</Typography>
                            </MenuItem>

                        </Menu>
                    </Box>
                    {/* Menú ICON */}


                    <IconButton onClick={()=>scrollToSection("Inicio")} aria-label="Ver imagen" sx={{ display: { xs: 'flex', md: 'none', flexGrow:1 } }}>
                        <img src={process.env.PUBLIC_URL + "/Assets/logos/scenario-logo-color.png"} alt="logo scenario" style={{ height: '30px', width: 'auto' }} />
                    </IconButton>

                   



                    <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' }, justifyContent: "center" }}>
                        {pages.map((page) => (
                            <Button
                                key={page}
                                onClick={()=>scrollToSection(page)}
                                sx={{ my: 2, color: 'dimgray', display: 'block' }}
                            >
                                {page}
                            </Button>
                        ))}
                    </Box>


                    {/* Button tienda */}

                    <Button
                        onClick={() => scrollToSection("Tienda")}
                        sx={{ my: 2, color: 'dimgray', display: 'block',mr:"1rem" ,display:{ xs: 'none', md: 'flex' } }}
                    >
                        Tienda
                    </Button>

                    <a /* href="https://scenariojobs.scenariovr.co/autenticacion" */ rel="noopener noreferrer" onClick={()=>{setOpenDialog(true)}}>
                        <button className="btn-login">Ingresar</button>
                    </a>

                    <IconButton onClick={abrirWhatsApp} aria-label='Whatsapp' sx={{display: { xs: 'none', md: 'flex' }, ml:"0.5rem"}}>
                        <WhatsAppIcon sx={{fontSize:"2rem", color:"#25d366"}}/>
                    </IconButton>
                    


                </Toolbar>
            </Container>

            <CustomizedDialogs open={openDialog} setOpen={setOpenDialog}></CustomizedDialogs>
        </AppBar>
    );
}
export default ResponsiveAppBar;